.enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.enterActive {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.exit {
  transform: translateY(0);
  opacity: 1;
}

.exitActive {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}
